* {
  margin: 0;
  padding: 0;
}

body {
  height: calc(100vh - 2em);
  color: #444;
  font-family: Helvetica, Arial, Sans;
  font-size: 16px;
}

a, a:visited {
  color: #3131e6;
}

.footer {
  text-align: center;
}

.totp-generator {
  max-width: 500px;
  width: 90%;
  margin: 2em auto 0;
}

.otp {
  text-align: center;
  height: 1em;
  padding: .2em 1em;
  font-size: 3em;
  font-weight: 700;
}

.qr {
  text-align: center;
}

.input {
  margin: .8em 0;
}

.input input, .input label {
  flex-direction: column;
  display: flex;
}

.input label {
  font-weight: 700;
}

.input input {
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #bbb;
  border-radius: .2em;
  padding: .4em .7em;
}

.otp-progress {
  width: 100%;
  margin-top: .5em;
  overflow: hidden;
}

.otp-progress .otp-progress-bar {
  height: .5em;
  border-radius: .5em;
  transition: width .2s;
  display: block;
}

.otp-progress .progress-text {
  color: #666;
  margin: .5em 0;
  font-size: .8em;
}

input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  appearance: textfield;
}

